import React from 'react';
import { themeDefault } from '../../themeDefault';
import './button.css';

export const Button = (props) => {
  let mainColor = themeDefault.palette.pastel[props.themeColor].main
  let lightColor = themeDefault.palette.pastel[props.themeColor].light
  let darkColor = themeDefault.palette.pastel[props.themeColor].dark
  let textColor = themeDefault.palette.getContrastText(mainColor)
  let styling = {
    color: textColor,
    backgroundColor: darkColor,
  };
  if (props.variant === "concave") {
    styling = {
      ...styling,
      backgroundImage: `linear-gradient(145deg, ${darkColor}, ${mainColor} 50%, ${lightColor} 85%)`,
    }
    if (!props.groupButton) {
      styling = {
        ...styling,
        boxShadow: `4px 4px 8px ${darkColor}, -4px -4px 8px ${lightColor}`,
      }
    }     
  } else if (props.variant === "convex") {
    styling = {
      ...styling,
      backgroundImage: `linear-gradient(145deg, ${lightColor} 10%, ${mainColor} 60%, ${darkColor})`,
    }
    if (!props.groupButton) {
      styling = {
        ...styling,
        boxShadow: `4px 4px 8px ${darkColor}, -4px -4px 8px ${lightColor}`,
      }
    }  
  } else if (props.variant === "flat") {
    styling = {
      ...styling,
      backgroundColor: mainColor,
    }
    if (!props.groupButton) {
      styling = {
        ...styling,
        boxShadow: `4px 4px 8px ${darkColor}, -4px -4px 8px ${lightColor}`,
      }
    } 
  } else if (props.variant === "inset") {
    if (props.groupButton) {
      styling = {
        ...styling,
        background: 'transparent', 
      }
    } else {
      styling = {
        ...styling,
        background: mainColor, 
        boxShadow: `4px 4px 8px ${darkColor} inset, -4px -4px 8px ${lightColor} inset`,
      }
    }
  } else if (props.variant === "outset") {
    if (props.groupButton) {
      styling = {
        ...styling,
        background: 'transparent'
      }
    } else {
      styling = {
        ...styling,
        background: mainColor, 
        boxShadow: `4px 4px 8px ${lightColor} inset, -4px -4px 8px ${darkColor} inset`,
      }
    }
  } else if (props.variant === "basic") {
    styling = {
      ...styling,
      color: themeDefault.palette.getContrastText(darkColor)
    }
  }
  let classNames = 'button';
  styling = {...styling, ...props.styling}
  console.log(styling)
  if (props.rounded) {
    classNames = [classNames, 'rounded'].join(' ');
  }
  return (
    <button type="button" className={classNames} style={styling}>
      {props.label}
    </button>
  )
}
