import React from "react";
import { themePalette } from "../../themePalette";

import { Button } from './Button';

export default {
  title: "Interaction Components/Button",
  component: Button,
  decorators: [
    (Story, { globals: { themeColor }}) => (
      <div style={{ 
        padding: '3rem 5rem', 
        backgroundColor: themePalette[themeColor].main,
        borderRadius: '50%' }}>
        <Story />
      </div>
    )
  ],
  parameters: {
    layout: 'centered'
  },
  argTypes: {
    label: {
      control: 'text',
    },
    variant: {
      control: { type: 'select' },
      options: ['concave', 'convex', 'flat', 'inset', 'outset', 'basic']
    },
    groupButton: {
      control: 'boolean',
      defaultValue: false, 
    },
    rounded: {
      control: { type: 'boolean' },
      defaultValue: true,
    },
    styling: { type: 'object' } 
  }
}

const Template = (args, { globals: { themeColor }} ) => 
  <Button {...{...args, themeColor: themeColor }} />

export const Concave = Template.bind({});;
Concave.args = {
  label: "Label as Arg",
  variant: 'concave'
}

export const Convex = Template.bind({});;
Convex.args = {
  label: "Label as Arg",
  variant: 'convex'
}

export const Flat = Template.bind({});;
Flat.args = {
  label: "Label as Arg",
  variant: 'flat'
}

export const Inset = Template.bind({});;
Inset.args = {
  label: "Label as Arg",
  variant: 'inset'
}

export const Outset = Template.bind({});;
Outset.args = {
  label: "Label as Arg",
  variant: 'outset'
}

export const Basic = Template.bind({});
Basic.args = {
  label: "Label as Arg",
  variant: 'basic'
}


